	module.exports = {"data":{"markdownRemark":{"html":"<p>The <code>getElementById()</code> method returns the element that has the id attribute with the specified value. It takes one argument, which is a case-sensitive string of the id for the element you want.</p>\n<p>This method is one of the most common methods in the HTML DOM, and is used almost every time you want to manipulate, or get info from, an element in your document. Here’s a simple example of the syntax:</p>\n<p><strong>HTML content:</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>demo<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p><strong>JavaScript content:</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>document<span class=\"token punctuation\">.</span><span class=\"token function\">getElementById</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"demo\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// Returns the element with id \"demo\"</span>\n</code></pre>\n      </div>\n<p>If you have more than one element with the same value of <code>id</code> (bad practice!), <code>getElementById</code> will return the first element found:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>demo<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>First<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>demo<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>Second<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>document<span class=\"token punctuation\">.</span><span class=\"token function\">getElementById</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"demo\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// Returns the element with id \"demo\" containing 'First'</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href='https://developer.mozilla.org/en-US/docs/Web/API/Document/getElementById' target='_blank' rel='nofollow'>document.getElementById()</a></p>\n<h4>Alternative solutions:</h4>\n<p>A commonly-used alternative to <code>document.getElementById</code> is using a jQuery selector which you read about more <a href=\"https://github.com/freeCodeCamp/guides/tree/master/src/pages/jquery\">here</a>.</p>","fields":{"slug":"/javascript/html-dom-getelementbyid-method/"},"frontmatter":{"title":"HTML DOM getElementById Method"}}},"pathContext":{"slug":"/javascript/html-dom-getelementbyid-method/"}}

